"use strict";

import Swiper from "swiper";

import SwiperCore, { Navigation, Pagination, Thumbs } from "swiper/core";

SwiperCore.use([Navigation, Pagination, Thumbs]);

import input from "./modules/input";
import modals from "./modules/modals";
import scrollInto from "./modules/scroll-Into";
import scrollingUp from "./modules/scrolling-up";
import textarea from "./modules/textarea";
import adaptive from "./modules/adaptive-header";

window.addEventListener("DOMContentLoaded", () => {
  const swiper = new Swiper(".slider-thumbs", {
    spaceBetween: 2,
    direction: "horizontal",
    slidesPerView: 2,
    freeMode: true,
    watchSlidesVisibility: true,
    watchSlidesProgress: true,
    breakpoints:{
      1000: {
        slidesPerView: 3,
        spaceBetween: 5,
        direction: "vertical",
      },
      
    }
  });
  const swiper2 = new Swiper(".slider-top", {
    spaceBetween: 10,
    loop: true,
    navigation: {
      nextEl: ".portfolio-button-next",
      prevEl: ".portfolio-button-prev",
    },

    pagination: {
      el: ".swiper-pagination",
      clickable: true,
    },
    thumbs: {
      swiper: swiper,
    },
  });

  const swiper3 = new Swiper(".swiper-container-2", {
    // Optional parameters
    
    spaceBetween: 25,
    loop: false,

    // If we need pagination
    pagination: {
      el:".swiper-pagination-2",
      clickable: true,
    },

    // Navigation arrows
    navigation: {
      nextEl: ".responses__button-next",
      prevEl: ".responses__button-prev",
    },
  });

  input();
  modals();
  scrollInto();
  scrollingUp();
  textarea();
  adaptive();
});

