"use strict";
const modals = () => {
  const getModalBtns = document.querySelectorAll(".modal-btn"),
    getModal = document.querySelector(".modal"),
    getClose = document.querySelector(".modal__close");

  getModalBtns.forEach((element) => {
    element.addEventListener("click", () => {
      if (!getModal.classList.contains("modal-open")) {
        getModal.classList.add("modal-open");
        document.body.style.overflow = "hidden";
      }
    });
  });

  getClose.addEventListener("click", () => {
    if (getModal.classList.contains("modal-open")) {
      getModal.classList.remove("modal-open");
      document.body.style.overflow = "";
    }
  });

  getModal.addEventListener("click", (e) => {
    if (e.target === getModal) {
      getModal.classList.remove("modal-open");
      document.body.style.overflow = "";
    }
  });

  document.addEventListener("keydown", (e) => {
    if (e.code === "Escape" && getModal.classList.contains("modal-open")) {
      getModal.classList.remove("modal-open");
      document.body.style.overflow = "";
    }
  });
};

export default modals;
