"use strict";

const scrollInto = () => {
  const about = document.querySelector(".header__item:nth-child(1)"),
    aboutFooter = document.querySelector(".footer__link:nth-child(1)"),
    responses = document.querySelector(".header__item:nth-child(2)"),
    features = document.querySelector(".header__item:nth-child(3)"),
    portfolio = document.querySelector(".header__item:nth-child(4)"),
    portfolioFooter = document.querySelector(".footer__link:nth-child(2)"),
    workFooter = document.querySelector(".footer__link:nth-child(3)"),
    contactUs = document.querySelector(".header__item:nth-child(5)"),
    scrollToAbout = document.querySelector(".about"),
    scrollToResponses = document.querySelector(".responses"),
    scrollToFeatures = document.querySelector(".features"),
    scrollToPortfolio = document.querySelector(".portfolio"),
    scrollToContactUs = document.querySelector(".contact-us"),
    scrollToWork = document.querySelector(".work");

  const getHeaderMenu = document.querySelector(".header__menu"),
  getHeader–†amburger = document.querySelector(".hamburger");

  function removeClassActive() {
    getHeader–†amburger.classList.remove('active');
      getHeaderMenu.classList.remove('active');
      document.body.style.overflow = "";
  }

  //main
  about.addEventListener("click", () => {
    scrollToAbout.scrollIntoView({ block: "center", behavior: "smooth" });
    removeClassActive();
  });
  responses.addEventListener("click", () => {
    scrollToResponses.scrollIntoView({ block: "center", behavior: "smooth" });
    removeClassActive();
  });
  features.addEventListener("click", () => {
    scrollToFeatures.scrollIntoView({ block: "center", behavior: "smooth" });
    removeClassActive();
  });
  portfolio.addEventListener("click", () => {
    scrollToPortfolio.scrollIntoView({ block: "center", behavior: "smooth" });
    removeClassActive();
  });
  contactUs.addEventListener("click", () => {
    scrollToContactUs.scrollIntoView({ block: "center", behavior: "smooth" });
    removeClassActive();
  });

  //footer

  portfolioFooter.addEventListener("click", () => {
    scrollToPortfolio.scrollIntoView({ block: "center", behavior: "smooth" });
  });
  workFooter.addEventListener("click", () => {
    scrollToWork.scrollIntoView({ block: "center", behavior: "smooth" });
  });
  aboutFooter.addEventListener("click", () => {
    scrollToAbout.scrollIntoView({ block: "center", behavior: "smooth" });
  });
};

export default scrollInto;
