"use strict";
const adaptive = () => {
  const getHeaderModal = document.querySelector(".header__modal-btn"),
    getHeaderContact = document.querySelector(".header__contact"),
    getHeaderNav = document.querySelector(".header__nav"),
    getHeader–†amburger = document.querySelector(".hamburger"),
    getHeaderMenu = document.querySelector('.header__menu');

  function resize() {
    let getWidth = document.documentElement.clientWidth;

    if (getWidth < 901) {
      getHeaderContact.appendChild(getHeaderModal);
    } else {
      getHeaderNav.appendChild(getHeaderModal);
    }
  }
  resize();

  window.addEventListener("resize", () => {
    resize();
  });

  getHeader–†amburger.addEventListener('click',()=>{
    if (!getHeader–†amburger.classList.contains('active')){
      getHeader–†amburger.classList.add('active');
      getHeaderMenu.classList.add('active');
      document.body.style.overflow = "hidden";
    }else{
      getHeader–†amburger.classList.remove('active');
      getHeaderMenu.classList.remove('active');
      document.body.style.overflow = "";
    }
    
  });


};
export default adaptive;
